.App {
  text-align: center;
}
body{
  background-image: url('./images/pexels-content-pixie-2736499.jpg');
background-size: cover;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h1{
  text-align: center;
  font-family: 'Sigmar', cursive;
    color: rgb(214, 23, 32);
    letter-spacing: 1.3px;
    font-weight: 900;
}
.todo{
  border: solid;
  text-align: center;
  height: 600px;
  width: 400px;
  margin-left: 40%;
  background-color: rgba(255, 255, 255, 0.15);
   box-shadow: 0 8px 32px 0 rgba(46, 16, 4, 0.37);
   backdrop-filter: blur(8.5px);
   border-radius: 10px;
   color: rgb(117, 50, 36);
   text-transform: uppercase;
}
.btn {
  box-shadow: none;
  width: 20%;
  height: 40px;
  background-color: #3d92dc;
  color: #fff;
  border-radius: 20px;
  box-shadow: 3px 3px 3px #b1b1b1,
      -3px -3px 3px #020f09;
  letter-spacing: 1.3px;
  
}

.btn:hover {
  background-color: #8b96cc;
}
.select{
  box-shadow: none;
  width: 40%;
  height: 40px;
  background-color: #788794;
  color: #f0e0e0;
  
  box-shadow: 3px 3px 3px #b1b1b1,
      -3px -3px 3px #020f09;
  letter-spacing: 1.3px;
}

.bt{
  box-shadow: none;
  width: 20%;
  height: 30px;
  background-color: #a8bbdc;
  color: #12120e;
  
  box-shadow: 3px 3px 3px #b1b1b1,
      -3px -3px 3px #020f09;
  letter-spacing: 1.3px;
  border-radius: 5px;
  margin-left: 10px;
}